body, html {
		font-family: "Roboto", sans-serif;
		margin:0px;
        padding:0px;
     	font-size:16px;
     	background:#fff;
     	height:100%
}
 
.no-padding   {
	 padding:0 !important; 
 }

header { 
		position:absolute;
		width:100%;
		z-index:1000;
		//background-color:rgba(0,0,0,0.4);
		transition:.3s;
		top:0;
		height:70px;
		//border-bottom:1px solid rgba(0,0,0,0.6);
		
		
		.container {

			height:100%;
			nav {
				height:100%;
				line-height:100%;
				width:100%;
				padding:0px 0px;
			   @keyframes animate-logo {
					from {
						opacity:0
					}
					to {
						opacity:1
					}
				}

			.logo {
				float:left;
				height:35px;
				margin:22px 0px;
				opacity:0;
				cursor:pointer;
				animation:animate-logo forwards;
				animation-duration: 1s;
				animation-delay:1s;
			}

			ul {
				display:block;
				float:right;
				list-style:none;
				margin:0;
				padding:0;
				height: 100%;

				li {
					display:inline-block;
					height: 100%;
					text-align:center;
					padding: 0;
					margin: 0;
					a {
						color:#fff;
						font-weight:300;
						transition:.2s;
						text-decoration:none;
						line-height: 70px;
						padding: 0 25px;
						letter-spacing:0px;
						font-size:1rem;

						&.active {
							font-weight:bold;
							color:#fff;
							text-decoration:none;
							margin: 0; 
							padding: 0 !important; 
							letter-spacing:1px;

						}
					}
				}
			}
		}

	}
		
}	
 
footer {
	color: #fff;
	background:$primary-color;
	z-index:10;
	position:relative;
	padding:100px 0px;
	font-size:0.8rem;
	h2{
		font-weight:300;
	}
	a{
		color:#fff;
		font-weight:bold;
		text-decoration:none;
		&:hover {
			text-decoration:none;
			color:#fff;
		}
	}
	p.contact-info {
	
		display:inline-block;
		vertical-align:top;
		margin:60px 0px;
		text-align:center;
		padding:0px 20px;
		.svg-img {
			
			display:block;
			width:50px;
			margin:0 auto 50px auto;

			height:auto;
			
		}	
	}
	
}
	
main{  
    position:relative;
    width:100%;
    min-height:100vh;
    overflow-x:hidden;
	
    section {
        position:relative;
    }
    @keyframes animate-screen {
    	from {top: 0;}
    	to {top: 100%;}
	}
    section.animate-in-screen {
		position:absolute;
		width:100%;
		height:100%;
		background-color:$primary-color;
		bottom:0;
		top:0;
		z-index:200;
		animation:animate-screen forwards;
		animation-duration: 0.5s;
		animation-delay:1s;
		z-index:2001;

	   @keyframes animate-logo-in {
					from {
						opacity:1
					}
					to {
						opacity:0
					}
				}

			.logo {
				position:absolute;
				top:50%;
				left:50%;
				transform: translate(-50%, -50%);
				width:350px;
				opacity:1;
				cursor:pointer;
				transition:.3s;
				z-index:2;
				animation:animate-logo-in forwards;
				animation-duration: 0.5s;
				animation-delay:0.5s;
			}
    } 
    section.slide, footer {  
    	float:left;
    	width:100%;
         overflow:hidden;
    	 position:relative;

         h2 {
             font-weight:300;
			margin-bottom:40px;
         }
    } 
    .slide.slide-1 {  
    	background-color:#121212;
		width:100%;
		min-height:calc(100vh - 300px); 
		overflow:hidden;
		video.fullscreen {  
			position: fixed;  
			top: 50%; 
			left: 50%;
			min-width: 100%; 
			min-height: 100%;
			width: auto;
			height: auto;   
			transform: translate(-50%, -50%);
			z-index:1;
		}

		.center-height {
			z-index:2;
			position:relative;
            top:20vh;
			h1 {
				position:relative;
				padding-left:70px;
				font-weight:100;
				font-size:3rem;
				line-height:4rem;
				color:#fff;
				text-shadow: 2px 2px 1px rgba(0,0,0,0.2);
		
				b {
					font-weight:normal;
				}

					@keyframes bounce {
						0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
						40% {-webkit-transform: translateY(-10px);transform: translateY(-10px);}
						60% {-webkit-transform: translateY(-5px);transform: translateY(-5px);}
					}
					.bouncer {
						  width: 0;
						  height: 0;
						  left:0;
						  border-left: 15px solid transparent;
						  border-right: 15px solid transparent;
						  border-top: 20px solid #fff;
						  cursor: pointer;
						  position: absolute;
							margin-top:0px;
						  -webkit-animation-duration: 2s;
						  animation-duration: 2s;
						  -webkit-animation-fill-mode: both;
						  animation-fill-mode: both;
						  -webkit-animation-name: bounce;
						  animation-name: bounce;
						  -webkit-animation-iteration-count: infinite;
						  animation-iteration-count: infinite;
					}
				img.logo {
					height:auto;
					width:15%;
					min-width:150px;
					float:left;
					max-width:200px;
					display:inline-block;
					padding-top:20px;
					padding-bottom:30px;
					padding-right:40px;
					margin-bottom:0px;
				}
			}
		}
		

    }
    .slide.slide-2 {
        background-color:$primary-color;
    	z-index:5;
    	overflow:visible;
		.animation-thingy {
			min-height:100px;
			z-index:2; 
			position:absolute;
			left:0;
    		bottom:0;
    		top:0;
			.scanner {
				position:absolute;
				left:0;
				bottom:0;
					.bottom-part {
						width:300px;
						height:100px;
						background:#333;
						padding:25px 20px;
						display:block;
						bottom:25px;
						z-index:1;
						left:0;
						position:absolute;
						border-top-left-radius:5px;
						border-top-right-radius:5px;
					}
					.ticket-scanner {
						width:270px;
						height:100px;
						//background:#555;
						//padding:25px 20px;
						//display:inline-block;
						position:absolute;
						bottom:0;
						z-index:3;
						div.laser-1 {
							height:1px;
							bottom:5px;

						}
						div.laser-2 {
							height:2px;
							bottom:10px;
						}
						div.laser-3 {
							height:10px;
							bottom:15px;

						}
						div.laser {
							position:absolute;					
							background:rgba(255,255,255,0.5);
							left:-20px;
							right:0px;

							-webkit-animation-name: pulsate;
							-webkit-animation-duration: 1s;
							-webkit-animation-timing-function: ease-in-out;
							-webkit-animation-iteration-count: infinite
						}
						@-webkit-keyframes pulsate {
							0% { background:rgba(255,255,255,0.4); }
							10% { background:rgba(255,255,255,0.5); }
							20% { background:rgba(255,255,255,0.4); }
							30% { background:rgba(255,255,255,0.6); }
							40% { background:rgba(255,255,255,0.6); } 
							50% { background:rgba(255,255,255,0.4);}
							60% { background:rgba(255,255,255,0.6);}
							70% { background:rgba(255,255,255,0.5);}
							80% { background:rgba(255,255,255,0.5);}
							90% { background:rgba(255,255,255,0.4);}
							100% { background:rgba(255,255,255,0.5);}
						 }
						div.shadow {
							height:5px;
							position:absolute;
							top:-5px;
							background:rgba(0,0,0,0.2);
							left:0px;
							right:0px;
						}
						div.counter {
							position:absolute;
							display:block;
							width:260px;
							background:#94b3b2;
							height:50px;
							border-radius:5px;
							.title {
								font-weight:100;
								color:#ddd;
								position:absolute;
								top:-15px;
								font-size:12px;
								left:0px;
							}
							.digits {
								color:#333;
								position:relative;
								display:block;
								font-size:40px;
								line-height:50px;
								width:100%;
								letter-spacing:2px;
								font-family:"Digit", "Roboto"

							}
						}
						span.light {
							position:absolute;
							top:-25px;
							right:0px;
							width:35px;
							height:35px;
							border:10px solid #555;
							border-radius:2px;
							background:red;
							transition:.3s;
						}
					}

			}
			
			.ticket-card {
				width:250px;
				z-index:2;
				height:auto;
				display:block;
				bottom:250px;
				top:-100px;
				left:0px;
				position:absolute;
				
				
			}

		}
		.container {
			position:relative;
			color:#fff;
			font-size:1rem;
			padding:100px 0px 250px 0px;
			h2{
				color:#fff;			
			}
			p {
				font-size:1.8rem;
				line-height:2.5rem;
				font-weight:100;
			}
		}

    }
    .slide.slide-3 {
        padding:80px 0px;
 

		background: #fff;
    	
   		z-index:6;
   		.fullscreen {
			position:absolute;
			top:0;
			bottom:0;
			left:0;
			right:0;
   			background-repeat:no-repeat!important;
			
			width:100%;
			height:100%; 
   		}
   		.container {
   			position:relative;
   			z-index:7;
   			.logos {
   				margin-right:20px;
   				padding-right:20px;
   				border-right: 1px solid #ddd;
   				float:left;
   				width:200px;
   				img {
   					float:left; 
   				}
   			}
   			
			.seat-animated {
				position:absolute;
				right:0;
				top:0;
				bottom:0;
				img {
					height:100px;
					width:auto;
				}
				.seatMy {
					position:absolute;
					top:0px;
				}
				.seatEmpty {
					position:absolute;
					bottom:0;	
				}
			}

   		}

    }
    .slide.slide-4 {
        width: 100%;
        height:calc(100% - 70px);
		min-height:700px!important;
       	background:$primary-color;
       	z-index:8;
		margin-bottom: -70px;
		
		.stateController {
			height:calc(100vh - 70px);
			width:100%;
			position:absolute;
			.state {
				width:100%;
				height:100%;
				float:left;
			}
		}
		.slide-container {
			width: 600%; /* to contain 6 panels, each with 100% of window width */
			height: 100vh;
		}
		.pin {
			width: 100%;
       		height:calc(100vh - 70px)!important;
       		

			-webkit-perspective: 1000;
		        perspective: 1000;
       		//top:70px!important;

		}
		
   		.panel {
   			height: 100%;
			width: calc(100% /6); /* relative to parent -> 25% of 400% = 100% of window width */
			float: left;
			
			background-size:cover!important;
        	transition: background 0.5s linear;
   		
			&.FCKDK {
				background-image: url(../images/Clubs/BG/FCKDK.jpg);
			}
			&.FCMDK {
				background-image: url(../images/Clubs/BG/FCMDK.jpg);
			}
			&.Lech {
				background-image: url(../images/Clubs/BG/Lech.jpg);
			}
			&.Lechia {
				background-image: url(../images/Clubs/BG/Lechia.jpg);
			}
			&.UL {
				background-image: url(../images/Clubs/BG/UL.jpg);
			}
			&.Sparta {
				background-image: url(../images/Clubs/BG/Sparta.jpg);
			}
			
		}

   		.clients {
			padding:50px 0px 110px 0px;
   		}
		.logos {
			text-align:center;
			padding:30px 0px;
			position:absolute;
			bottom:0px;
			left:0;
			right:0;
			z-index:20;
			
			background:#fff;
			a {
				width: 90px;
				height: 90px;
				
				margin: 5px;
				transition:.2s;
				display:inline-block;
				&:hover {
					transform:scale(1.1);
				}
				img {
					max-width: 90px;
					height: auto;
					max-height: 90px;
					width: auto;
					display:block;
				}
			}
		}
    }

}


@media (min-width:1001px), (min-height: 701px) {
	.menuTrigger,	.menuTriggerLabel {
		  display:none;
	}
	
	header {
			&.menuOnTop {
			//box-shadow:0px 0px 4px rgba(0,0,0,0.2);
			background:#fff;
			position:fixed;
			top:0;
			border:none;
			.container {
				nav {
					img.logo {
						display:inline-block;
						height:24px;
					}
					ul {
						
						li a {
							color:$primary-color;
							font-size:1rem;
							&:hover {

								color:$primary-color
							}
						}

					}
					
				}

			}
			
		}

	}
}


@media (max-width: 1000px), (max-height: 700px) {

	body, html {
		font-size:14px;
	  	//overflow-x: hidden;
		width:100%;
		height:100%;
	}
  	input.menuTrigger {
		display:none;
	}
	.menuTrigger + .menuTriggerLabel, main, header {
	  transition: left 0.2s;
	}

	.menuTrigger:checked + .menuTriggerLabel  {
	  left: 215px;
	}


	header { 
		position:fixed;
		left:-200px;
		bottom:0;
		z-index:2000;
		top:0;
		width:200px;
		height:100%;
		background:#fff;
		box-shadow:1px 2px 2px rgba(0,0,0,0.2);

		.container {
			width:100%;
			nav {
				img.logo {
					display:inline-block;
					height:24px;
				}
				ul {
					float:left;
					width:100%;
					li{
						text-align:left;
						width:100%;
						display:block;
						height:auto;
						float:left;
						a{
							color:$primary-color;
							font-weight:300;
							transition:.3s;
							text-decoration:none;
							line-height: 30px;
							padding: 0px;
							display:block;
							width:100%;
							padding:10px 0px;
							letter-spacing:0px;
							font-size:1rem;
						}
					}
				}
			}
		}
		.menuTrigger:checked ~ & {
			left:0px;
		}
	}

   
	.menuTriggerLabel {
		  /* critical styles - hide the checkbox input */
	      position: fixed;
	      top:20px;
	      left:20px;
	      z-index:2001;
          height: 30px;
          width: 30px;
          cursor: pointer;
          background-image: url("data:image/svg+xml;utf8,
          <svg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' version='1.1' x='0px' y='0px' width='30px' height='30px' viewBox='0 0 30 30' xml:space='preserve'>
 			 <rect fill='#ddd' width='30' height='6'/>
 			 <rect fill='#ddd' y='24' width='30' height='6'/>
 			 <rect fill='#ddd' y='12' width='30' height='6'/>
		 </svg>");
          background-size: contain;
	}
   
  

	main {
	
		min-width: 100%;
		min-height: 100%;
		background-color: #fff;
		position: relative;
		top: 0;
		bottom: 100%;
		left: 0;
		z-index: 2;

		 &:before {
					
                	content: "";
	                background: rgba(0,0,0,0.0);
	                top: 0;
	                left: 0px;
	                bottom: 100%;
	                right: 0;
	                position: absolute;
	                display: block;
	                z-index: 20;
	                height: 100%;
	                opacity:0;
	                visibility:hidden;
	                transition: 0.2s;

		}
		.menuTrigger:checked ~ & {
			//left: 200px;
            z-index:1;

            &:before {
					
                	content: "";
	                background: rgba(0,0,0,0.6);
	                top: 0;
	                left: 0px;
	                bottom: 100%;
	                right: 0;
	                position: absolute;
	                display: block;
	                z-index: 20;
	                height: 100%;
	                opacity:1;
					visibility:visible;

            }
		}


		.slide.slide-1 {  
			min-height:calc(100vh - 100px); 
			.center-height {
					top:20vh;

				h1 {
					font-size:2rem;
					line-height:3rem;
					
					img.logo {
							width:40%;
							max-width:90px;
							min-width:90px;
							float:none;
							display:block;
							padding-bottom:0;
					}
				}
			}

			
		}

		 .slide.slide-3 {
       		padding:100px 0px;
			.container {
				background:rgba(255,255,255,0.8);
				padding:20px;
				margin:20px;
				z-index:9
			}
    }
	
	}

}